import React from 'react'

function MenuItem({menu}) {
 return (
  <article>
   {
    menu.map((item)=>{
     const {id , title , price , img , desc}  = item
     return(
      <div className="menuitem" key={id}>
    <div className="imgContainer displayinline">
     <img src={img} alt={title}/>
    </div>
    <div className="dataContainer displayinline">
     <div className="Mailheader">
      <h4>{title}</h4>
      <p>${price}</p>
     </div>
     <div className="info">
      <p>{desc}</p>
     </div>
    </div>
   </div>
     )
    })
   }
   
  </article>
 )
}

export default MenuItem
