import React , {useState,useEffect} from 'react'
import MenuItem from  './MenuItem'
import Catagories from './Catagories'
import './style.css'
import data from './data'

// get unique catagori items 
const allCatagories = ['all' , ...new Set(data.map((item)=> item.category))]

function Allmenu() {
 const [menu , setMenu] = useState(data);
 const [catagories] = useState(allCatagories);

 // function for filtring menu by catagorie 
 const FilterItems = (catagorie)=>{
  if(catagorie === 'all'){
   setMenu(data);
  }else{  
  const newItems = data.filter((item)=> item.category === catagorie)
  setMenu(newItems);
  }
 }
 useEffect(()=>{
  document.title='Our Menu'
 })
 return (
  <main className='container'>
   <div className="header">
    <h1>Our Menu</h1>
    <div className="theLine"></div>
   </div>
   <div className="catagoriesontainer">
    <Catagories catagories={catagories} FilterItems={FilterItems} />
   </div>
   <div className="menuContiner">
    <MenuItem menu={menu}/>
   </div>
  </main>
 )
}

export default Allmenu
